import 'regenerator-runtime/runtime'

const faceLandmarksDetection = require('@tensorflow-models/face-landmarks-detection');

require('@tensorflow/tfjs-backend-wasm');

// 清除 canvas 
function clearCanvas() {   
    var c = document.getElementById("output")
    var cxt= c.getContext("2d");
    cxt.clearRect(0,0,c.width,c.height);  
};

// 计算两点间距离
function distance(a, b) {
    // = (a[0]-b[0])^2 + (a[1]-b[1])^2
    // Math.pow(a,b) 返回a的b次方， Math.sqrt(a,b)返回a的b次根
    // 此处该函数用于用于返回平面坐标上两点距离.
    return Math.sqrt(Math.pow(a[0] - b[0], 2) + Math.pow(a[1] - b[1], 2));
}

// 该函数用于画点
function drawPoint(ctx, face) {
    try {
        // mesh 是裁剪后图片中的脸部坐标
        // let a = face.mesh; 
        // .scaledMesh 是放缩后的坐标，对应实际传入图片中的位置（这个可以直接拿来用）
        let a = face.scaledMesh;
        let points = [];
        a.forEach(e => {
            points.push( [e[0], e[1]].map(Math.abs) )
        });
        for (let i = 0; i < points.length; i++) {
            const x = points[i][0];
            const y = points[i][1];
    
            ctx.beginPath();
            ctx.arc(x, y, 1 /* radius */, 0, 2 * Math.PI);
            ctx.fill();
        }
    } catch (error) {
        // 存在获取不到脸部特征的情况
    }
};

// 绘制脸部方框
function getFaceBox(ctx, face) {
    try {
        // 获取 boundingBox
        var bottomRight, topLeft, width, height, origin_x, origin_y;
        bottomRight = face.boundingBox.bottomRight.map(Math.abs);
        topLeft = face.boundingBox.topLeft.map(Math.abs);
        width = topLeft[0] - bottomRight[0];
        height = topLeft[1] - bottomRight[1];
        origin_x = bottomRight[0];
        origin_y = bottomRight[1];
        // .fillRect 填充矩形 .strokeRect 绘制矩形边框
        ctx.strokeRect(origin_x, origin_y, width, height);
    } catch (error) {
        // 
    }
};

// 渲染相关线框
async function renderPrediction() {
    // console.log('draw', new Date())
    const predictions = await window.model.estimateFaces({
        input: document.querySelector("video"),
        flipHorizontal: true,
        predictIrises: false
    });

    // 如果正常，则此时 video 图像渲染在 canvas 中
    ctx.drawImage(
        video, 0, 0, videoWidth, videoHeight, 0, 0, canvas.width, canvas.height);
    
    if (predictions){
        const face = predictions[0];
        // console.log(face)
        // 描绘脸框
        getFaceBox(ctx, face);

        // 然后画散点
        drawPoint(ctx, face);
    }

    // console.log(window.animation_status)
    if (window.animation_status){
        requestAnimationFrame(renderPrediction);
    } else {
        console.log('Stop!')
        clearCanvas();
    }
}

// 主函数
async function main() {
    // 开启摄像头
    // let a = setupCamera()
    // a.then(()=> console.log('a', a))
    // 这里返回的是一个 Promise 对象: Promise {<fulfilled>: video#video}
    // 所以这里 resolve 定义了一个 fullfilled 的 Promise 对象
    // await a 
    await setupCamera();

    // 通过 await 代替了 then 保证当异步操作完成时再执行下一行。
    const model = await faceLandmarksDetection.load(
        faceLandmarksDetection.SupportedPackages.mediapipeFacemesh
        ,{
            "maxFaces":1,
        }
    );
    console.log('model', model)
    window.model = model;

    // 绘制图像
    window.animation_status = true;
    window.rafID = renderPrediction()
}

window.main = main;
main();